
body{
    box-sizing: content-box;
    font-family: sans-serif;
    margin: 0 5%;
}


/*----- Single Institute------------ */
.header{
    align-items: center;
}

/* Ranking comp */
.info-section{
    margin-top: 35px;
    padding-bottom: 20px;
    border-bottom: 2px solid gray;
}
.rankings{
    padding: 15px;
    background-color: aquamarine;
    border-radius: 10px;
    margin-left: 30px;
}
.rankings p{
    margin-left: 10px;
}
.rankings span, .test-overview span{
    background-color: white;
    padding: 10px;
    margin-left: 10px;
    display: inline-block;
    min-width: 50px;
}

/*  Test */
.test-overview{
    padding:20px;
    background: linear-gradient(#ca0861 50%, #ff0076);;
    margin-right: 35px;
    width: 30%;
}
.test-overview p span{
    font-weight: bold;
    color: darkcyan;
    margin: 0;
}
.test-overview p{
    font-weight: bold;
    color: bisque;
}
.test-overview p:nth-child(n+2){
    display: flex;
    justify-content: space-between;
    align-items: center;
}


/* Course info*/
.course-overview{
    background-color: whitesmoke;
    padding: 30px;
}

.course-overview p,.course-overview  li{
    line-height: 1.5em;
    color: #d2173a;
}

.course-overview div{
    justify-content: space-between;
}
.course-overview div p{
    color: royalblue;
    font-size: 30px;
}
.course-overview div p span{
    color: rgb(20, 20, 20);
    font-weight: bold;
    font-size: 20px;
}
/* Anecdotes */
 
.anecdotes {
    
    margin-top: 30px;
    float: left;
    margin-right: 30px;
}
.anecdotes h3{
    text-align: center;
}
.anecdotes > div{
    border: 3px solid pink;
    border-radius: 20px;
    color: rgb(1, 1, 77);
    padding: 20px 30px;
    min-height: 50px;
    width: 700px;
    margin: auto;
}